body{
    overflow-x: hidden;
}
.footer{
    display: flex;
    justify-content: space-around;
}

.f-font{
    font-family: 'Proxima Nova' sans-serif;
font-style: normal;
font-weight: normal;
font-size: 20px;
line-height: 32px;
/* identical to box height, or 160% */
letter-spacing: 0.02em;
color: #919299;
margin: 0 !important;
}

.f-h1{
    font-family: 'Proxima Nova' sans-serif;
font-style: normal;
font-weight: bold;
font-size: 20px;
line-height: 32px;
/* identical to box height, or 160% */
letter-spacing: 0.02em;
color: #919299;
margin: 0 !important;
}

.icons{
    display: flex;
}

#icon{
    font-size: 25px;
    height: 41px;
    width: 41px;
    border-radius: 50%;
    color: #A7A7A7;
    background: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 0.7rem;
    box-shadow: 0 1px 1px rgb(0 0 0 / 25%), 0 5px 5px rgb(0 0 0 / 10%);
}


#icon-pink{
    font-size: 25px;
    height: 41px;
    width: 41px;
    border-radius: 50%;
    background: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-right: 0.7rem;
    color: #F78CB6 !important;
    box-shadow: 0 1px 1px rgb(0 0 0 / 25%), 0 5px 5px rgb(0 0 0 / 10%);
}

.f-h2{
    font-family: 'Circular Std' sans-serif;
font-style: normal;
font-weight: 500;
font-size: 24px;
line-height: 32px;
/* identical to box height, or 133% */
letter-spacing: 0.02em;
color: #56597A;
margin: 0 !important;
}

.f-a{
    font-family: 'Proxima Nova' sans-serif;
font-style: normal;
font-weight: normal;
font-size: 20px;
line-height: 32px;
/* identical to box height, or 160% */
text-decoration: none;
letter-spacing: 0.02em;
margin: 0 !important;
color: #919299;
}

ul{
    margin: 0 !important;
}


.copyright{
    background: #F78CB6;
    color: white;
    display: flex;
    justify-content: space-between;
    margin: 0 !important;
    width: 100% !important;
    position: absolute;
    left: 0;
}
.c-1{
    padding: 1rem 6.5rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.c-2{
    padding: 1rem 9.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
}
.c-2 select{
    background: #F78CB6;
    border: none;
    color: white;
    outline: none;
}

