
/* Header */

.header{
    display: flex;
    padding: 5rem 0 !important;
}

.header-left{
    flex: 5;
}
.header-right{
    flex: 7;
    text-align: center;
}



/* Services */

.card{
background: #FFF1F9;
border-radius: 8px;
padding-top: 4rem !important;
padding-left: 2.5rem;
padding-right: 2.5rem;
padding-bottom: 2rem;
}

.card:hover{
    background: #F78CB6;
}
.card:hover >p{
color: white !important;
background: #F78CB6;
}
.card:hover >a{
    color: white;
    text-decoration: none;
}
.card:hover > #icons{
    color: white;
}

.cardheader{
    display: flex;
}

#icons{
    height: 50px;
    width: 50px;
    font-size: 48px;
    color: #56597A;
    padding-bottom: 2.5rem;
}

.ser-btn{
    display: flex;
    align-items: center;
    justify-content: center;
}




/* Design */

.header-right-design{
    flex: 7;
    margin-left: 10rem;
}


/* Testimonal */
.header-left-testi{
    padding-right: 5rem;
}

.testi{
    padding-left: 5rem !important;
    padding-right: 5em !important;
}



/* Brand */
.brand-img{
margin-right: 5rem;
padding: 3rem 0;
width: 157px;
cursor: pointer;
}

.brand1{
    margin-right: 5rem;
    cursor: pointer; 
}

.brand5{
    padding: 3rem 0;
    cursor: pointer; 
}

.brands{
    display: flex;
    justify-content: space-between;
}


/* Email */
.header-email{
    text-align: center;
    padding: 10rem 0 !important;
}

.email-form{
border-radius: 4px;
border: none;
background: #ffffff;
font-size: 15px;
display: flex;
justify-content: center;
align-items: center;
padding: 0.5rem 0;
width: 706px;
margin: auto;
box-shadow: 0 1px 1px rgb(0 0 0 / 25%), 0 5px 5px rgb(0 0 0 / 10%);
}

.email-input{
border-radius: 4px;
border: none;
font-size: 20px;
padding: 5px 10px;
margin-right: 10rem;
margin-left: 0.3rem;
}

.email-btn{
    padding: 0.5rem 1.5em;
    border: none;
    cursor: pointer;
    font-family: 'Circular Std', sans-serif;
    font-style: normal;
    font-weight: 450;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.02em;
    background: #F78BB6;
border-radius: 4px;
color: white;
}
