/* Animations */

/* Border X get width  */
.nav-nav li{
    position: relative;
}
.borderXwidth li:before, .borderXwidth li:after
{
    position: absolute;
    opacity: 0;
    width: 0%;
    height: 2px;
    content: '';
    background: #F78CB6;
    transition: all 0.3s;
}

.borderXwidth li:before
{
    left: 0px;
    top: 0px;
}

.borderXwidth li:after
{
    right: 0px;
    bottom: 0px;
}

.borderXwidth li:hover:before, .borderXwidth li:hover:after
{
    opacity: 1;
    width: 100%;
}

 

